.home {
  padding: 80px 0;
  background-color: #000000;

  @include tablet {
    padding: 70px 0;
  }

  @include small-tablet {
    padding: 60px 0;
  }

  @include mobile {
    padding: 45px 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

    @include tablet {
      flex-direction: column;
      gap: 60px;
    }

    @include mobile {
      gap: 35px;
    }
  }

  &__content {
    flex: 0 1 50%;

    @include tablet {
      flex: 1 1 100%;

    }
  }

  &__img {
    flex: 0 1 50%;

    @include tablet {
      flex: 1 1 100%;
    }

    img {
      display: block;
      object-fit: cover;
      border-radius: 5px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    max-width: 450px;
    font-weight: 700;
    font-size: 56px;
    line-height: 120%;
    text-transform: capitalize;
    color: #FFFFFF;

    @include tablet {
      max-width: 100%;
      font-size: 52px;
    }

    @include small-tablet {
      max-width: 100%;
      font-size: 42px;
    }

    @include mobile {
      font-size: 32px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: #666666;

    @include tablet {
      margin-bottom: 40px;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 16px;
      margin-bottom: 20px;
    }
  }
}
