.features {
  padding: 80px 0;
  background-color: var(--light-color);

  @include tablet {
    padding: 60px 0;
  }

  @include small-tablet {
    padding: 50px 0;
  }

  @include mobile {
    padding: 30px 0 40px 0;
  }

  &__heading {
    margin-bottom: 60px;
    justify-content: center;

    @include tablet {
      margin-bottom: 45px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 24px;

    @include tablet {
      margin-bottom: 15px;
    }
  }

  &__title {
    margin-left: auto;
    margin-right: auto;
    max-width: 952px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 40px;
    row-gap: 20px;

    @include tablet {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
      row-gap: 15px;
    }

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
      row-gap: 30px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      column-gap: 0;
      row-gap: 35px;
    }
  }

  &__item {
    padding: 20px;
    background-color: transparent;

    @include small-tablet {
      padding: 0;
    }

    &-img {
      width: 60px;
      height: 60px;
      margin: 0 auto;
      margin-bottom: 22px;

      @include mobile {
        margin-bottom: 15px;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 16px;
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      text-align: center;
      letter-spacing: 0.2px;
      text-transform: capitalize;
      color: var(--dark-color);

      @include small-tablet {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 12px;
      }

      @include mobile {
        margin-bottom: 12px;
      }
    }

    &-descr {
      margin: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color: #616161;

      @include mobile {
        font-size: 14px;
      }
    }
  }
}
