.footer {
  padding: 100px 0;
  background-color: #3563E9;

  @include tablet {
    padding: 50px 0 40px 0;
  }

  @include small-tablet {
    padding: 40px 0 30px 0;
  }

  &__row {
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
    padding-bottom: 47px;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      padding-bottom: 40px;
    }
  }

  &__nav {
    display: flex;
    gap: 138px;

    @include tablet {
      gap: 150px;
      margin-bottom: 40px;
    }

    @include small-tablet {
      gap: 50px;
      margin-bottom: 30px;
    }

    @include mobile {
      flex-wrap: wrap;
      gap: 45px;
      margin-bottom: 25px;

      &-column:nth-child(1) {
        flex: 0 0 100%;
      }
    }
  }

  &__nav-title {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 16px;
    line-height: 28px;
    color: #FFFFFF;

    @include mobile {
      margin-bottom: 12px;
      font-size: 15px;
      line-height: 25px;
    }
  }

  &__nav-item:not(:last-child) {
    margin-bottom: 12px;
  }

  &__nav-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    opacity: 0.7;
    transition: opacity 0.3s ease;

    @include mobile {
      font-size: 14px;
      line-height: 16px;
    }

    &:hover {
      opacity: 1;
    }
  }

  &__subscribe {
    display: flex;
    flex-direction: column;
    width: 338px;

    padding: 30px 42px;
    background-color: #2851E3;

    @include tablet {
      width: 100%;
      padding: 25px 30px;
    }

    @include small-tablet {
      width: 100%;
      padding: 15px 20px;
    }

    &-title {
      margin-bottom: 18px;
      font-weight: 700;
      font-size: 16px;
      line-height: 28px;
      color: #FFFFFF;

      @include small-tablet {
        margin-bottom: 12px;
      }
    }
  }

  &__form {
    position: relative;
    margin: 0;
    margin-bottom: 18px;

    @include small-tablet {
      margin-bottom: 14px;
    }
  }

  &__form-label {
    width: 100%;
  }

  &__form-input {
    padding: 0 15px;
    width: 100%;
    height: 50px;
    border-radius: 5px;
    background-color: var(--light-color);
    transition: box-shadow 0.3s ease;

    @include small-tablet {
      padding: 0 12px;
      height: 46px;
    }

    &:focus {
      box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
    }

    &::placeholder {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      align-items: center;
      letter-spacing: 0.2px;
      color: #BDBDBD;
    }
  }

  &__form-btn {
    position: absolute;
    content: "";
    bottom: 0;
    right: 0;
    width: 50px;
    height: 50px;
    background-image: url('../img/arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-color: #3563E9;
    border-radius: 0px 3px 3px 0px;

    @include small-tablet {
      width: 46px;
      height: 46px;
    }
  }

  &__form-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: #FFFFFF;
    opacity: 0.7;

    @include small-tablet {
      line-height: 18px;
    }
  }

  &__column {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 47px;

    @include tablet {
      padding-top: 40px;
    }

    @include small-tablet {
      flex-direction: column;
      gap: 25px;
      padding-top: 30px;
    }

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 1px;
      top: 0;
      left: 0;
      right: 0;
      background: #FFFFFF;
      opacity: 0.06;
      border: 1px solid #FFFFFF;
    }
  }

  &__copy-list {
    display: flex;
    align-items: center;
  }

  &__copy-item:not(:last-child) {
    margin-right: 36px;
  }

  &__copy-link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    transition: text-decoration 0.3s ease;

    &:hover {
      text-decoration: underline;
    }
  }

  &__logo {
    width: 211px;
    height: 53px;

    @include tablet {
      width: 176px;
      height: 45px;
    }
  }
}

.social {
  display: flex;
  align-items: center;
  gap: 15px;

  &__item {
    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.3);
    }
  }
}
