.title {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 48px;
  line-height: 114%;
  text-align: center;
  text-transform: capitalize;
  color: var(--dark-color);

  @include tablet {
    margin-bottom: 10px;
    font-size: 36px;
  }

  @include small-tablet {
    font-size: 30px;
  }

  @include mobile {
    font-size: 24px;
    margin-bottom: 12px;
  }
}

.descr {
  margin: 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  text-align: center;
  color: var(--grey-color);

  @include tablet {
    font-size: 18px;
  }

  @include small-tablet {
    font-size: 16px;
  }
}

.subtitle {
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  text-align: center;
  color: var(--grey-color);

  @include tablet {
    font-size: 18px;
  }

  @include small-tablet {
    font-size: 16px;
  }
}
