.about {
  padding: 80px 0;
  background-color: var(--light-color);

  @include tablet {
    padding: 70px 0;
  }

  @include small-tablet {
    padding: 60px 0 50px 0;
  }

  @include mobile {
    padding: 45px 0 40px 0;
  }

  &__heading {
    margin-bottom: 72px;

    @include tablet {
      margin-bottom: 60px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__item {
    margin: 0 auto;
    max-width: 980px;
    display: flex;
    align-items: center;
    gap: 100px;

    @include tablet {
      gap: 40px;
    }

    @include small-tablet {
      flex-direction: column;
    }

    @include mobile {
      gap: 25px;
    }

    &-img {
      min-width: 451px;
      height: 369px;

      @include tablet {
        min-width: 380px;
        height: auto;
      }

      @include mobile {
        min-width: auto;
      }

      img {
        display: block;
        object-fit: cover;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 32px;
      font-weight: 700;
      font-size: 36px;
      line-height: 120%;
      text-transform: capitalize;
      color: var(--dark-color);

      @include tablet {
        margin-bottom: 20px;
        font-size: 28px;
      }

      @include mobile {
        margin-bottom: 12px;
        font-size: 20px;
      }
    }

    &-descr {
      margin: 0;
      font-weight: 500;
      font-size: 18px;
      line-height: 150%;
      color: #666666;

      @include mobile {
        font-size: 16px;
      }
    }
  }

  &__item:not(:last-child){
    margin-bottom: 45px;
  }

  &__item:nth-child(even) {
    flex-direction: row-reverse;

    @include small-tablet {
      flex-direction: column;
    }
  }
}
