.btn {
  display: inline-flex;
  padding: 0 40px;
  font-weight: 600;
  font-size: 18px;
  color: var(--light-color);
  line-height: 55px;
  text-align: center;
  letter-spacing: 0.46px;
  background-color: var(--accentLight-color);
  transition: background-color 0.3s ease;

  @include mobile {
    padding: 0 25px;
    font-size: 15px;
    line-height: 46px;
  }

  &:hover {
    background-color: var(--accent-color);
  }
}
