.reviews {
  padding: 80px 0;

  @include tablet {
    padding: 60px 0;
  }

  @include mobile {
    padding: 50px 0 40px 0;
  }

  &__heading {
    margin-bottom: 50px;

    @include tablet {
      margin-bottom: 30px;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__content {
    margin: 0 auto;
    max-width: 1094px;
    padding: 20px;

    @include tablet {
      padding: 0;
    }
  }
}

.swiper-slide {
  margin: 0 auto;
}

.reviews__item {
  display: flex;
  flex-direction: column;
  padding: 26px 10px;

  &-star {
    margin: 0 auto 27px auto;

    @include mobile {
      margin: 0 auto 15px auto;
    }
  }

  &-text {
    margin-bottom: 27px;
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.2px;
    text-transform: capitalize;
    color: #424242;

    @include mobile {
      margin-bottom: 20px;
      font-size: 16px;
    }
  }

  &-human {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &-avatar {
    margin-bottom: 8px;
    display: block;
    width: 40px;
    height: 40px;

    img {
      object-fit: cover;
    }
  }

  &-name {
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.2px;
    text-transform: capitalize;
    color: var(--grey-color);
  }
}


.swiper-button-prev,
.swiper-button-next {
  --swiper-theme-color: #212121;
  --swiper-navigation-size: 30px;
  transition: color 0.2s ease-in;

  &:hover {
    --swiper-theme-color: #2851E3;
  }
}

.swiper-pagination-bullets {
  --swiper-theme-color: #212121;
}

.swiper-pagination-bullet-active-main {
  --swiper-theme-color: #2851E3;
}
