.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: var(--accent-color);
  padding: 22px 0;

  @include tablet {
    padding: 15px 0;
  }

  @include mobile {
    padding: 12px 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__btn {
    padding: 0 22px;
    font-weight: 500;
    font-size: 16px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.46px;
    color: var(--accent-color);
    background-color: var(--light-color);
    border-radius: 5px;
    transition: color 0.3s ease, background-color 0.3s ease;

    @include mobile {
      padding: 0 18px;
      font-size: 14px;
      line-height: 28px;
    }

    &:hover {
      color: var(--light-color);
      background-color: var(--dark-color);
    }
  }
}

.lang-btn {
  display: flex;
  align-items: center;
  gap: 7px;

  @include small-tablet {
    display: none;
  }
}

.lang__name {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  color: var(--light-color);
}

.nav {
  @include small-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--accent-color);
    opacity: 0;
    visibility: visible;
    transition: opacity 0.3s ease, visibility 0.3s ease;


  }

  &__list {
    display: flex;
    align-items: center;

    @include small-tablet {
      justify-content: center;
      flex-direction: column;
      align-items: center;
      width: 100%;
      height: 100%;
    }
  }

  &__item:not(:last-child) {
    margin-right: 30px;

    @include tablet {
      margin-right: 20px;
    }

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 50px;
    }
  }

  &__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.2px;
    color: #E0E0E0;
    transition: color 0.3s ease;

    @include tablet {
      font-size: 12px;
      line-height: 16px;
    }

    @include small-tablet {
      font-size: 20px;
      line-height: 30px;
    }

    &:hover {
      color: var(--light-color);
    }
  }
}

.link-active {
  font-weight: 700;
  color: var(--light-color);
  margin-right: 15px;

  @include small-tablet {
    margin-right: 0;
  }
}

.burger {
  @include burger;
  z-index: 1000;
  height: 20px;
  color: #fff;
  @media (min-width: 768px) {
    display: none;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
