.updates {
  padding: 80px 0;
  background-color: var(--light-color);

  @include tablet {
    padding: 40px 0;
  }

  @include mobile {
    padding: 20px 0;
  }

  &__column {
    display: flex;
    align-items: center;

    padding: 60px 90px;
    gap: 50px;
    background-color: var(--accent-color);
    border-radius: 15px;

    @include tablet {
      padding: 35px 45px;
      gap: 35px;
    }

    @include small-tablet {
      padding: 30px 35px;
      flex-direction: column;
      gap: 30px;
    }

    @include mobile {
      padding: 30px 25px;
    }
  }

  &__content {
    flex: 0 1 50%;

    @include tablet {
      flex: 0 1 70%;
    }

    @include small-tablet {
      flex: 1 1 100%;
    }
  }

  &__img {
    flex: 0 1 50%;

    @include tablet {
      flex: 0 1 30%;
    }

    img {
      display: block;
      object-fit: cover;
    }

    @include mobile {
      flex: 1 1 100%;
    }
  }

  &__heading {
    margin-bottom: 48px;

    @include tablet {
      margin-bottom: 25px;
    }
  }

  &__title {
    text-align: left;
    color: var(--light-color);

    @include small-tablet {
      text-align: center;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: left;
    letter-spacing: 0.2px;
    text-transform: capitalize;
    color: var(--light-color);

    @include small-tablet {
      text-align: center;
    }
  }
}

.form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  gap: 16px;

  @include mobile {
    flex-direction: column;
    gap: 12px;
  }

  &__label {
    position: relative;
    width: 100%;

    &::before {
      content: '';
      position: absolute;
      top: 10px;
      left: 15px;
      width: 24px;
      height: 24px;
      background: url('../img/mail.svg') no-repeat;
      background-size: contain;

      @include mobile {
        top: 10px;
        left: 12px;
        width: 20px;
        height: 20px;
      }
    }
  }

  &__input {
    padding: 0 49px;
    width: 100%;
    height: 44px;
    border-radius: 5px;
    background-color: var(--light-color);
    transition: box-shadow 0.3s ease;

    @include mobile {
      padding: 0 42px;
      height: 40px;
    }

    &:focus {
      box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
    }

    &::placeholder {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      align-items: center;
      letter-spacing: 0.2px;
      color: #BDBDBD;

      @include mobile {
        font-size: 15px;
        line-height: 16px;
      }
    }
  }

  &__btn {
    padding: 0 22px;
    height: 44px;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.46px;
    color: #FFFFFF;
    background-color: #000000;
    border-radius: 5px;
    transition: color 0.3s ease, background-color 0.3s ease;

    @include mobile {
      height: 40px;
      font-size: 15px;
      line-height: 16px;
    }

    &:hover {
      color: #000000;
      background-color: #fff;
    }
  }
}
